import { useRoutes, Routes, Route, Navigate, useLocation } from "react-router-dom";
// 
import { FilterRoute } from "./ProtectedRoute";

// &Layout
import GlobalComponent from "./GlobalComponent";


import { Suspense, lazy } from "react";

// 404()
import Page404 from "pages/Page404"

// Auth Page-----------------------------------------------------
import Home from "pages/Home" // 
import Profile from "pages/Profile" // 

// -----------------------------------------------------
const Users = lazy(() => import('pages/Users')); // 
const ApiConnect = lazy(() => import('pages/ApiConnect')); // 17-1. >API
const ChartTest = lazy(() => import('pages/ChartTest'));

// pathname '/' 
export const RemoveTrailingSlash = ({ ...rest }) => {
    const location = useLocation()

    // If the last character of the url is '/'
    if (location.pathname.match('/.*/$')) {

        // console.log(`slash is detected`, location.pathname);

        return <Navigate replace {...rest} to={{
            pathname: location.pathname.replace(/\/+$/, ""),
            search: location.search
        }} />
    } else return null
}

// 
export default function CrossbotRouter() {

    const routePageList = [
        // Auth Page-----------------------------------------------

        {
            path: "/",
            // protected ==> <FilterRoute mode="protected">
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <Home />
                    </GlobalComponent>
                </FilterRoute>
        },
        {
            path: "/home",
            // protected ==> <FilterRoute mode="protected">
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <Home />
                    </GlobalComponent>
                </FilterRoute>
        },
        { // 
            path: "/profile",
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <Profile />
                    </GlobalComponent>
                </FilterRoute>
        },
        { // 
            path: "/users",
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <Users />
                    </GlobalComponent>
                </FilterRoute>
        },
        { // >API
            path: '/apiConnect',
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <ApiConnect />
                    </GlobalComponent>
                </FilterRoute>
        },
        { // >API
            path: '/apiManage',
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <ApiConnect mode="edit" />
                    </GlobalComponent>
                </FilterRoute>
        },

        
        { // 
            path: '/chartTest',
            element:
                <FilterRoute mode="free">
                    <GlobalComponent layout="backend">
                        <ChartTest />
                    </GlobalComponent>
                </FilterRoute>
        },


        {
            path: "*",
            element: <Page404 />
        }
    ];

    const routeList = routePageList.map((pageItem, index) => {
        return (
            (<Route key={`route_${index}`} path={pageItem.path} element={pageItem.element} />)
        )
    });

    const routes = (
        <Suspense fallback={<p> Loading...</p>}>
            <RemoveTrailingSlash />
            <Routes>
                {routeList}
            </Routes>
        </Suspense>
    );

    return routes;
};