import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import Backend from "i18next-http-backend";

i18n
    //  i18next-http-backend
    .use(Backend)
    //  i18next  react-i18next 
    .use(initReactI18next)
    //  initReactI18next
    .init({
        backend: {
            //
            loadPath: "/locales/{{lng}}/{{ns}}.json",
        },
        //  fallbackLng (en) 
        fallbackLng: "en",
        // 
        lng: "zh-TW",
        interpolation: {
            //  escaped  xss  React.js  false
            escapeValue: false,
        },
        // jsonnamespace( http://localhost:3000/locales/zh-tw/newNamespace.json public )
        // namespacenavigate
        ns: ['login', 'menu', 'navBar', 'lang', 'profile', 'component',
            'users', 'setting', 'pay', 'order', 'social', 'dataCollection',
            'tag', 'apiConnect'],
        // defaultNS: 'login', // translation.json
    }, (err, t) => {
        if (err) return console.error('i18n error', err);
        // t('hello'); // -> same as i18next.t
    });

export default i18n;